import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { DetalleActividad, DetalleActividadEstado, DetalleActividadModi, DetalleActividadMostrar } from '../../actividad/models/detalleactividad';
import { Subject } from 'rxjs';
import { EstudianteService } from '../service/estudiante.service';
import { DataTableDirective } from 'angular-datatables';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { ActividadService } from '../../actividad/service/actividad.service';
import { ActividadEstadoModi } from '../models/estudiante';
import Swal from 'sweetalert2';
import { Actividad } from '../../actividad/models/actividad';

@Component({
  selector: 'app-listadetalle',
  templateUrl: './listadetalle.component.html',
  styleUrls: ['./listadetalle.component.scss']
})
export class ListadetalleComponent implements OnInit {
  detalle: DetalleActividadMostrar
  detalles: DetalleActividadMostrar[]
  dtModi:DetalleActividad
  estadoDetalle:DetalleActividadEstado
  id:number
  idActividadParam:number
  estadoActividad:ActividadEstadoModi
  @Input()idActividad:number
  @Input()estudianteId:number
  @ViewChild(DataTableDirective, { static: false })
  dtElement: DataTableDirective;
  dtOptions: any = {};
  dtTrigger: Subject<Actividad> = new Subject<Actividad>();
  // dtTrigger: Subject<any> = new Subject<any>();
  // dtOptions: DataTables.Settings = {};
  // dtTrigger: Subject<any> = new Subject<any>();

  constructor(
    private estudianteServicio: EstudianteService,
    private cambioEstadoDtServicio: EstudianteService,
    private actiEstadoService: ActividadService,
    private ro: Router
  ) { }


  ngOnInit(): void {
    this.loadTable()
    this.cargarDetalles(this.estudianteId,this.idActividad);
  }
  loadTable() {
    this.dtOptions = {
      searching: false,
      columnDefs: [
        { orderable: false, targets: [0,6] },
        { searchable: false, targets: [0] },
        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: "//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json",
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: "full_numbers",
      responsive: true,
    };

  }
  cargarDetalles(id: number, idactividad: number) {
    this.idActividadParam=idactividad;//obteniendo el id de la actividad para usarlo en el cambio d estado
    this.estudianteServicio.mostrarActividadesDetalleList(id, idactividad).subscribe(resp => {
      this.detalles = resp
      this.dtTrigger.next(null);
    })
  }

  cambiarEstado(detalleSeleccionado:DetalleActividadModi){
    this.id= detalleSeleccionado.id;
    if(detalleSeleccionado.estado=='Pendiente'){
      Swal.fire({
        title: '¿Está seguro de modificar el estado? ',
        text: `Esto se puede revertir`,
        icon: 'question',
        showCancelButton: true,
        confirmButtonColor: "#a90000",
      cancelButtonColor: '#343a40',
        confirmButtonText: 'Sí, cambiar',
        cancelButtonText:'Cancelar'
      }).then((result) => {
        if (result.isConfirmed) {
          this.cambiarEstadosActividades(this.idActividadParam,'Inactivo',this.id,'Finalizado');
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: 'El estado ha sido modificado',
            icon: 'success',
          });
          //this.reload();

        } else if (result.isDenied) {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: "Información",
            text: `Cambios no aplicados`,
            icon: "info",
          });
        }
      });
    }else if(detalleSeleccionado.estado=='Ejecucion'){
      Swal.fire({
        title: '¿Está seguro de modificar el estado? ',
        text: `Esto se puede revertir`,
        icon: 'question',
        showCancelButton: true,
        confirmButtonColor: "#a90000",
      cancelButtonColor: '#343a40',
        confirmButtonText: 'Sí, cambiar',
        cancelButtonText:'Cancelar'
      }).then((result) => {
        if (result.isConfirmed) {
          this.cambiarEstadosActividades(this.idActividadParam,'Inactivo',this.id,'Finalizado');
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: 'El estado ha sido modificado',
            icon: 'success',
          });
          //this.reload();

        } else if (result.isDenied) {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: "Información",
            text: `Cambios no aplicados`,
            icon: "info",
          });
        }
      });
    }else if(detalleSeleccionado.estado=='Finalizado'){

      Swal.fire({
        title: '¿Está seguro de modificar el estado? ',
        text: `Esto se puede revertir`,
        icon: 'question',
        showCancelButton: true,
        confirmButtonColor: "#a90000",
      cancelButtonColor: '#343a40',
        confirmButtonText: 'Sí, cambiar',
        cancelButtonText:'Cancelar'
      }).then((result) => {
        if (result.isConfirmed) {
          this.cambiarEstadosActividades(this.idActividadParam,'Activo',this.id,'Pendiente');
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: 'El estado ha sido modificado',
            icon: 'success',
          });
         // this.reload();

        } else if (result.isDenied) {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: "Información",
            text: `Cambios no aplicados`,
            icon: "info",
          });
        }
      });
    }
  }

  cambiarEstadosActividades(idActividad:number,estadoActividad:string,
    idDetalle:number,estadoDetalle:string){
      //cambiando el estado detalle
      this.estadoDetalle= new DetalleActividadEstado(estadoDetalle);
      this.cambioEstadoDtServicio.editarEstadoDt(idDetalle,this.estadoDetalle).subscribe((resp:any)=>{
        //cambiando el estado de la actividad
        this.estadoActividad= new ActividadEstadoModi(estadoActividad);
        this.actiEstadoService.editarEstadoAct(idActividad,this.estadoActividad).subscribe((r:any)=>{
          this.reload()
        });

      });

  }

  ngOnDestroy() {
    this.dtTrigger.unsubscribe();
  }
  reload() {
    if (this.dtElement.dtInstance) {
      this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
       // this.detalles=[];
        dtInstance.destroy();
        this.cargarDetalles(this.estudianteId,this.idActividad);
      });
    }
  }

  mostrarRecargando() {
    //recarga los datos
    let currentUrl = this.ro.url;
    this.ro.routeReuseStrategy.shouldReuseRoute = () => false;
    this.ro.onSameUrlNavigation = "reload";
    this.ro.navigate([currentUrl]);
  }

}
