import { Component, OnInit, ViewChild } from "@angular/core";
import { EstudianteService } from "../../service/estudiante.service";
import {
  C,
  Carrera,
  Estudiante,
  EstudianteList,
} from "../../models/estudiante";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import Swal from "sweetalert2";
import { DataTableDirective } from "angular-datatables";
import { Subject } from "rxjs";
import { isCaracter, isCarnet, isTel, telefonoRegex } from "src/app/constantes/constantesproyecto";
import { Router } from "@angular/router";

@Component({
  selector: "app-lista",
  templateUrl: "./lista.component.html",
  styleUrls: ["./lista.component.scss"],
})
export class ListaComponent implements OnInit {
  breadCrumbItems: Array<{}>;
  estudiantes: EstudianteList[] = [];
  estudiantesEstado: EstudianteList[] = [];
  lista: Estudiante[] = [];
  estudiante!: Estudiante;
  estudent!: EstudianteList;
  carreras!: Carrera[];
  formulario!: FormGroup;
  fCar!: FormGroup;
  fP!: FormGroup;
  estado: string = "Activo";
  listaActividades!: EstudianteList[];
  pdf: string;
  error = '';
  showLoading = false;
  /**DataTables */
  @ViewChild(DataTableDirective, { static: false })
  dtElement: DataTableDirective;
  dtOptions: any = {};
  dtTrigger: Subject<EstudianteList> = new Subject<EstudianteList>();
  dtTriggerr: Subject<EstudianteList> = new Subject<EstudianteList>();

  constructor(
    private estudianteServicio: EstudianteService,
    private modalService: NgbModal,
    private fb: FormBuilder,
    private fbC: FormBuilder,
    private router: Router
  ) {
    (this.formulario = this.inif()),
      (this.fCar = this.inifC()),
      (this.fP = this.inifP());
  }
  ngOnInit(): void {
    this.breadCrumbItems = [
      { label: "Estudiante" },
      { label: "Listado", active: true },
    ];
    this.llenarCombo();
    this.dtOptions = {
      columnDefs: [
        { orderable: false, targets: [6] },
        { searchable: false, targets: [0, 6] },
        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: "//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json",
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: "full_numbers",
      responsive: true,
    };
    this.cargarDatos();
  }

  private inif(): FormGroup {
    return this.fb.group({
      nombres: [
        "",
        [
          Validators.required,
          Validators.pattern(isCaracter),
          Validators.minLength(4),
        ],
      ],
      carnet: ["", [Validators.required, Validators.minLength(4), Validators.pattern(isCarnet)]],
      apellidos: [
        "",
        [
          Validators.required,
          Validators.pattern(isCaracter),
          Validators.minLength(4),
        ],
      ],
      carrera: ["", [Validators.required]],
      telefono: ["", [Validators.required, Validators.pattern(telefonoRegex)]],
      id: [""],
    });
  }
  private inifC(): FormGroup {
    return this.fb.group({
      nombrecarrera: [
        "",
        [
          Validators.required,
          Validators.pattern(isCaracter),
          Validators.minLength(4),
        ],
      ],
    });
  }
  private inifP(): FormGroup {
    return this.fbC.group({
      pdf: ["", [Validators.required, ,]],
    });
  }
  esCampoValido(campo: string) {
    const validarCampo = this.formulario.get(campo);
    return !validarCampo?.valid && validarCampo?.touched
      ? "is-invalid"
      : validarCampo?.touched
        ? "is-valid"
        : "";
  }
  valid(campo: string) {
    const validarCampo = this.fCar.get(campo);
    return !validarCampo?.valid && validarCampo?.touched
      ? "is-invalid"
      : validarCampo?.touched
        ? "is-valid"
        : "";
  }
  seleccionar(campo: string) {
    const validarCampo = this.fP.get(campo);
    return !validarCampo?.valid && validarCampo?.touched
      ? "is-invalid"
      : validarCampo?.touched
        ? "is-valid"
        : "";
  }
  llenarCombo() {
    this.estudianteServicio.carrera().subscribe((resp: any) => {
      this.carreras = resp;
    });
  }
  estados(event: any) {
    this.estado = event.target.value;
    this.cargarConEstado();
  }
  cargarDatos() {
    this.showLoading = true;
    this.estudianteServicio.estudiantes().subscribe((resp: any) => {
      this.showLoading = false;
      this.estudiantes = resp;
      this.dtTrigger.next();
    });
  }
  cargarConEstado() {
    this.reload();
    this.estudianteServicio
      .estudiantesEstado(this.estado)
      .subscribe((response) => {
        if (this.estado === "Seleccione") {
          this.estudiantes = [];
          this.cargarDatos();
        } else
          if (this.estado === "todos") {
            this.cargarFull()
          } else {
            this.estudiantes = response;
            this.dtTrigger.next();
          }

      });
  }
  cargarFull() {
    this.estudianteServicio.estudiantesFull().subscribe((resp: any) => {
      this.estudiantes = resp;
      this.dtTrigger.next();
    });
  }
  openModal(content: any) {
    this.error = '';
    this.modalService.open(content, { backdrop: "static", keyboard: false });
    this.formulario.get("carnet").enable();
    this.estado;
  }
  openModalEdt(content: any, e: EstudianteList) {
    this.error = '';
    this.estudent = e;
    this.formulario.patchValue(e);
    this.formulario.get("carnet").disable();
    this.formulario.get("id").setValue(this.estudent.id);
    this.formulario.get("carrera").setValue(this.estudent.idCarrera);
    this.modalService.open(content, { backdrop: "static", keyboard: false });
  }
  openDetalle(e: EstudianteList) {
    this.estudent = e;
    this.router.navigate(['estudiante/actividades', this.estudent.id]);
  }
  openModalA(content) {
    this.modalService.open(content, {
      keyboard: false,
      scrollable: true,
      size: "lg",
    });
  }
  modal(content: any) {
    this.modalService.open(content, { keyboard: false, size: "sm", backdrop: 'static', });
  }

  guardar() {
    this.showLoading = true
    this.estado = null;
    const c: Carrera = new Carrera(this.formulario.get("carrera").value);
    const nombres = this.formulario.get("nombres").value;
    const apellidos = this.formulario.get("apellidos").value;
    const telefono = this.formulario.get("telefono").value;
    const carnet = this.formulario.get("carnet").value;
    this.estudiante = new Estudiante(
      nombres,
      apellidos,
      telefono,
      carnet.toUpperCase(),
      c
    );

    if (!telefono.match(telefonoRegex)) {
      return;
    } else if (this.existeTel(telefono)) {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: "Advertencia",
        text: `Teléfono ya registrado`,
        icon: "warning",
      });
      return;
    }
    else {
      this.estudianteServicio
        .guardar(this.estudiante)
        .subscribe((resp: any) => {
          if (resp) {
            this.showLoading = false
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: 'Éxito',
              text: `Almacenado correctamente`,
              icon: 'success',
            });
            this.reload();
            this.modalService.dismissAll();
            this.formulario.reset();
            this.cargarDatos();
          } else {
            this.showLoading = false
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: 'Advertencia',
              text: `Ocurrio un problema`,
              icon: 'warning',
            });
          }
        }, err => {
          if (err.status == 409) {
            this.showLoading = false
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: 'Advertencia',
              text: `Ya existe el carnet`,
              icon: 'warning',
            });
          }
          if (err.status == 400) {
            this.showLoading = false
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: "Advertencia",
              text: `Teléfono ya registrado`,
              icon: "warning",
            });
          }
        });
    }
    return Object.values(this.formulario.controls).forEach((control) =>
      control.markAsTouched()
    );
  }
  reset() {
    this.formulario.reset();
  }
  eliminar(e: EstudianteList) {
    this.estudent = e;
    if (this.estudent.usuario) {
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Error',
        text: "No puede ser eliminado",
        icon: 'warning',
      });
    } else {
      Swal.fire({
        title: "¿Está seguro de eliminar? ",
        text: `Esto no se puede revertir`,
        icon: "question",
        showCancelButton: true,
        confirmButtonColor: "#a90000",
        cancelButtonColor: '#343a40',
        confirmButtonText: "Sí, borrar",
        cancelButtonText: "Cancelar",
      }).then((result) => {
        if (result.isConfirmed) {
          this.estudianteServicio.eliminar(e).subscribe(
            (resp) => {
              console.log(resp);
              Swal.fire({
                confirmButtonColor: "#a90000",
                title: 'Eliminado',
                text: `El registro ha sido eliminado`,
                icon: 'success',
              });
              this.reload();
              this.cargarDatos();
            },
            (err: any) => {
              Swal.fire({
                confirmButtonColor: "#a90000",
                icon: "error",
                title: "Error",
                text: err.error.msg,
              });
            }
          );
        } else if (result.isDenied) {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: "Información",
            text: `Cambios no aplicados`,
            icon: "info",
          });
        }
      });
    }
  }
  editar() {
    this.showLoading = true
    const c: Carrera = new Carrera(this.formulario.get("carrera").value);
    const nombres = this.formulario.get("nombres").value;
    const apellidos = this.formulario.get("apellidos").value;
    const telefono = this.formulario.get("telefono").value;
    const carnet = this.formulario.get("carnet").value;
    this.estudiante = new Estudiante(
      nombres,
      apellidos,
      telefono,
      carnet.toUpperCase(),
      c,
      this.formulario.get("id").value
    );

    if (!telefono.match(telefonoRegex)) {
      return;
    }
    else {
      this.estudianteServicio.editar(this.estudiante).subscribe(
        (resp) => {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: `Modificado correctamente`,
            icon: 'success',
          });
          this.reload();
          this.modalService.dismissAll();
          this.reset();
          this.cargarDatos();
        },
        (err) => {
          if(err.status === 400){
            this.showLoading = false
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: "Advertencia",
              text: `Teléfono ya registrado`,
              icon: "warning",
            });
          }
        }
      );
    }
  }
  guardarCarrera() {
    this.showLoading = true
    const c: C = new C(this.fCar.get("nombrecarrera").value);
    if (this.existeCarrera(c.nombrecarrera.toLowerCase())) {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Advertencia',
        text: `Ya existe la carrera`,
        icon: 'warning',
      });
      return;
    } else {
      this.estudianteServicio.guardarCarrera(c).subscribe((resp) => {
        if (resp) {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: `Almacenado correctamente`,
            icon: 'success',
          });
          this.reload();
          this.cargarDatos();
          this.llenarCombo();
          this.fCar.reset();
        } else {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Advertencia',
            text: `Ocurrio un problema`,
            icon: 'warning',
          });
        }
      });
    }
    return Object.values(this.fCar.controls).forEach((control) =>
      control.markAsTouched()
    );
  }
  reload() {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      // this.estudiantes = [];
      dtInstance.destroy();
      // this.cargarDatos()
    });
  }
 existeCarrera(nombre: string): boolean {
    const normalizedNombre = nombre.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
    return this.carreras.some(carrera => {
      const normalizedCarrera = carrera.nombrecarrera.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
      return normalizedCarrera.toLocaleLowerCase() === normalizedNombre.toLocaleLowerCase();
    });
  }
  existeTel(telefono: string): boolean {
    return this.estudiantes.some((estudiante) => estudiante.telefono === telefono);
  }

  generar(event: any) {
    this.pdf = event.target.value;
  }
  imprimir() {
    if (this.pdf === "selec") {
      Swal.fire({
        confirmButtonColor: "#a90000",
        icon: "error",
        title: "Oops...",
        text: "Seleccione una opción",
      });
      return;
    }
    if (this.pdf === "todos") {
      this.descargarPdfFullEquipo();
    } else {
      this.descargarPdfParametro();
    }
  }
  descargarPdfParametro() {
    this.showLoading = true;
    this.estudianteServicio
      .generarPdfEstudiantexEstado(this.pdf)
      .subscribe((data: Blob) => {
        this.showLoading = false;
        const blob = new Blob([data], { type: "application/pdf" });
        const link = document.createElement("a");
        let url = URL.createObjectURL(blob);
        window.open(url);
      });
  }
  descargarPdfFullEquipo() {
    this.showLoading = true;
    this.estudianteServicio
      .generarPdfFullEstudiante()
      .subscribe((data: Blob) => {
        this.showLoading = false;
        const blob = new Blob([data], { type: "application/pdf" });
        let url = URL.createObjectURL(blob);
        window.open(url);
      });
  }
  ngOnDestroy() {
    this.dtTrigger.unsubscribe();
  }
}
