import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { EstudianteList } from '../../models/estudiante';
import { Subject } from 'rxjs';
import { EstudianteService } from '../../service/estudiante.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { ListadetalleComponent } from '../../listadetalle/listadetalle.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DataTableDirective } from 'angular-datatables';

@Component({
  selector: 'app-actividades',
  templateUrl: './actividades.component.html',
  styleUrls: ['./actividades.component.scss']
})
export class ActividadesComponent implements OnInit {
  //dtElement: DataTableDirective;
  @ViewChild(DataTableDirective, { static: false })
  dtElement: DataTableDirective;
  dtOptions: any = {};
 dtTriggerr: Subject<EstudianteList> = new Subject<EstudianteList>();
 listaActividades !: EstudianteList[]
  estudent !: EstudianteList
  id:number
  bandera:number;

  constructor(
    private estudianteServicio: EstudianteService,
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private ro: Router
  ) { }

  ngOnInit(): void {
    this.route.params.subscribe((params: Params) => {
       this.id = params['id'];
    });
    this.loadTable();
    this.cargarActividades();
  }
  loadTable(){
    this.dtOptions = {
      //searching: false,
      columnDefs: [
        { orderable: false, targets: [0,3] },
        { searchable: false, targets: [3] },
        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: "//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json",
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: "full_numbers",
      responsive: true,
    };
  }


  cargarActividades(){
    this.estudianteServicio.detallesEstudiante(this.id).subscribe(resp =>{
      this.listaActividades = resp
      this.dtTriggerr.next(null);
    })
  }

  ngOnDestroy() {
    this.dtTriggerr.unsubscribe();
  }

  OpenDetalle(content: any,estudiante: EstudianteList){
    this.estudent = estudiante
    this.modalService.open(content, { size: 'lg', backdrop: 'static', keyboard: false });
  }

  openModal(content) {
    this.modalService.open(content, {
      keyboard: false,
      scrollable: true, size: 'lg'
    });
  }

  /**Para recargar */
  reload() {
    if (this.dtElement.dtInstance) {
      this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
        // Destruye DataTables
        this.listaActividades = []
        dtInstance.destroy();
        // Vuelve a cargar los datos
        this.cargarActividades();
      });
    }
  }


  mostrarRecargando() {
    //recarga los datos
    let currentUrl = this.ro.url;
    this.ro.routeReuseStrategy.shouldReuseRoute = () => false;
    this.ro.onSameUrlNavigation = "reload";
    this.ro.navigate([currentUrl]);
  }


}
